import React, { FC, SyntheticEvent, useEffect, useState } from 'react';
import LZString from 'lz-string';
import { SYMBOL_LIST } from '../../constants/home.constants';
import HomeService from '../../services/home.service';

import './Home.scss';

const HomeComponent = () => {
  const [output, setOutput] = useState<string>('');
  const [input, setInput] = useState<string>('');
  const [key, setKey] = useState<string>('');
  const [symbolList, setSymbolList] = useState<string[]>([]);
  const [textArea, setTextArea] = useState<HTMLTextAreaElement | null>();
  
  const title = "Ð¢ÐµÑ€ÑÐ¹ÑÑ Ð’Ð°ÑÑ!";

  const decrypt = (output: string, key: string) => {
    setTimeout(() => {setInput(decryptMessage(output, key, symbolList))}, 500);
  }

  const encrypt = (input: string, key: string) => {
    setTimeout(() => {setOutput(encryptMessage(input, key, symbolList))}, 500);
  }

  const updateKey = (value: string) => {
    setKey(value);
    
    if (!value) {
      setOutput(input);
      return;
    }
    if (input) encrypt(input, value);
    if (!input && output) decrypt(output, value);
  }

  const updateInput = (value: string) => {
    setInput(value);

    if (key) encrypt(value, key);
    if (!key) setOutput(value);
  }

  const updateOutput = (value: string) => {
    setOutput(value);
    if (key) decrypt(value, key);
    if (!key) setInput(value);
  }

  const encryptMessage = (message: string, keyword: string, symbolList: string[]): string => {
    const maxId = symbolList.length - 1;
    const messageIdList = HomeService.stringToIdList(message, symbolList);
    const keywordIdList = HomeService.stringToIdList(keyword, symbolList);
    
    const eMessageIdList = HomeService.encryptIdList(messageIdList, keywordIdList, maxId);
    const eMessage = HomeService.idListToString(eMessageIdList, symbolList);
    
    console.log("ðŸš€ ~ file: Home.tsx ~ line 69 ~ decryptMessage ~ keywordIdList", keywordIdList); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 67 ~ decryptMessage ~ symbolList.length", symbolList.length); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 52 ~ encryptMessage ~ messageIdList", messageIdList.length, messageIdList); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 55 ~ encryptMessage ~ eMessageIdList", eMessageIdList.length, eMessageIdList); // test remove

    return eMessage ; //LZString.compressToUTF16(eMessage).trim();
  };

  const decryptMessage = (eMessage: string, keyword: string, symbolList: string[]): string => {
    //eMessage = LZString.decompressFromUTF16(eMessage) || 'error';

    const maxId = symbolList.length - 1;
    const eMessageIdList = HomeService.stringToIdList(eMessage, symbolList);
    const keywordIdList = HomeService.stringToIdList(keyword, symbolList);
    
    const messageIdList = HomeService.decryptIdList(eMessageIdList, keywordIdList, maxId);
    const message = HomeService.idListToString(messageIdList, symbolList);
    
    console.log("ðŸš€ ~ file: Home.tsx ~ line 69 ~ decryptMessage ~ keywordIdList", keywordIdList); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 67 ~ decryptMessage ~ symbolList.length", symbolList.length); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 52 ~ encryptMessage ~ messageIdList", messageIdList.length, messageIdList); // test remove
    console.log("ðŸš€ ~ file: Home.tsx ~ line 55 ~ encryptMessage ~ eMessageIdList", eMessageIdList.length, eMessageIdList); // test remove

    return message;
  };

  const copyCodeToClipboard = (textArea: HTMLTextAreaElement | null | undefined) => {
    if (!textArea) return;
    textArea.select()
    document.execCommand("copy")
  }


  useEffect(() => {
    setSymbolList(HomeService.prepareSymbolList(SYMBOL_LIST));
  }, []);

  return (
    <div className="home-wrapper">
      <div className="home">
        <div className="title">
          <h1>{title}</h1>
        </div>
        <div>
          <InputComponent type="input" label={'ÐšÐ»ÑŽÑ‡'} placeholder={'ÐºÐ»ÑŽÑ‡Ð¾Ð²Ðµ ÑÐ»Ð¾Ð²Ð¾ ...'} value={key} onChange={(e) => {updateKey((e.target as HTMLInputElement).value)}} />
          <div>
            <InputComponent label={'Ð¢ÐµÐºÑÑ‚'} placeholder={'ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ ...'} value={input} onChange={(e) => {updateInput((e.target as HTMLInputElement).value)}} />
            <InputComponent
              label={'Bitcoin'}
              placeholder={'Ð²Ñ‹Ñ…Ð»Ð¾Ð¿ Ñ‚ÑƒÑ‚...'}
              value={output}
              onChange={(e) => updateOutput((e.target as HTMLInputElement).value)} 
              setRef={(e) => setTextArea(e)}
            />
            <ButtonComponent value="copy Bitcoin" onClick={() => copyCodeToClipboard(textArea)} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomeComponent;

interface InputComponentProps {
  value: string;
  onChange: (event: SyntheticEvent) => void;
  type?: 'input' | 'textarea';
  label?: string;
  placeholder?: string;
  id?: string;
  setRef?: (elem: HTMLTextAreaElement | null) => void;
}

const InputComponent: FC<InputComponentProps> = ({
  type = 'textarea',
  label,
  placeholder,
  value,
  onChange = () => {},
  id,
  setRef = () => {}
}) => {
  return (
    <div className="input">
      {label && <label htmlFor={id}>{label}</label>}

      <div className="input-wrapper">
        {type === 'textarea' && (
          <textarea id={id} cols={30} rows={10} placeholder={placeholder} value={value} onChange={onChange} ref={(textarea) => setRef(textarea)}></textarea>
        )}
        {type === 'input' && (
          <input id={id} placeholder={placeholder} value={value} onChange={onChange} />
        )}
      </div>
    </div>
  );
};

interface ButtonComponentProps {
  value: string;
  onClick?: (e: SyntheticEvent) => void
};

const ButtonComponent: FC<ButtonComponentProps> = ({
  value = '',
  onClick = () => {},
}) => {
  return (
    <div className="button">
      <button onClick={onClick}>{value}</button>
    </div>
  );
};